import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Row, Card, Form, Input, Select } from 'antd';
import { classroom } from 'dump/data';

const { Option } = Select;
const { TextArea } = Input;

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    groundworkTraining: '',
    building: '',
    floor: '',
    numberRoom: '',
    capacity: '',
    numberStudent: '',
    roomType: '',
    roomDevice: '',
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'formTeacher' })
class FormPointLadderFour extends Component {
  state = {
    teacherId: false,
    initialValue: {},
  };

  componentDidMount = () => {
    const { match } = this.props;
    if (match.params.id) {
      this.setState({ teacherId: match.params.id });
      this.getTeacherById(match.params.id);
    }
  };

  getTeacherById = code => {
    const teacherMap = classroom.filter(teacher => teacher.code === code)[0];
    const initialValue = handleInitialValue(teacherMap);
    this.setState({ initialValue });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
      }
    });
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { teacherId } = this.state;
    return (
      <Card title={teacherId ? 'Cập nhật danh mục phòng học' : 'Thêm danh mục phòng học'}>
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
            <Form.Item hasFeedback label="Cơ sở đào tạo" {...formItemLayout}>
              {getFieldDecorator('groundworkTraining', {
                initialValue: initialValue.groundworkTraining,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <Select>
                  <Option value="aaa">Cầu Giấy</Option>
                </Select>
              )}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Tòa nhà" hasFeedback>
              {getFieldDecorator('building', {
                initialValue: initialValue.building,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <Select>
                  <Option value="aaa">A1</Option>
                  <Option value="bbb">A2</Option>
                  <Option value="ccc">A3</Option>
                </Select>
              )}
            </Form.Item>
            <Form.Item hasFeedback label="Tầng" {...formItemLayout}>
              {getFieldDecorator('floor', {
                initialValue: initialValue.floor,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <Select>
                  <Option value="aaa">1</Option>
                  <Option value="bbb">2</Option>
                  <Option value="ccc">3</Option>
                </Select>
              )}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Số phòng" hasFeedback>
              {getFieldDecorator('numberRoom', {
                initialValue: initialValue.numberRoom,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item hasFeedback label="Sức chứa" {...formItemLayout}>
              {getFieldDecorator('capacity', {
                initialValue: initialValue.capacity,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Số sv" hasFeedback>
              {getFieldDecorator('numberStudent', {
                initialValue: initialValue.numberStudent,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item hasFeedback label="Loại phòng" {...formItemLayout}>
              {getFieldDecorator('roomType', {
                initialValue: initialValue.roomType,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <Select>
                  <Option value="aaa">Hội trường</Option>
                  <Option value="bbb">Phòng làm việc</Option>
                  <Option value="ccc">Thực hành</Option>
                </Select>
              )}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Thiết bị phòng" hasFeedback>
              {getFieldDecorator('roomDevice', {
                initialValue: initialValue.roomDevice,
                rules: [
                  {
                    // required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<TextArea rows={4} />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel href="#/classroom/list" />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormPointLadderFour {...props} />
  </Page>
));



